.blocks {
    // margin-bottom: 100px
    padding: 80px 0 120px;
    @include r($t-lg) {
        padding-top: 56px; }
    @include r($m-lg) {
        padding-top: 0;
        margin-bottom: 12px; }
    &__title {
        margin-bottom: 24px;
        @include r($t-lg) {
            margin-bottom: 22px; }
        @include r($m-lg) {
            margin-bottom: 20px; } }
    &__elements {
        display: flex;
        flex-wrap: wrap; }
    &__el {
        max-width: 550px;
        @include r($t-lg) {
            margin-bottom: 42px; }
        @include r($m-lg) {
            margin-bottom: 38px; } }
    &__text {
        max-width: 472px; } }

